@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  /* background-color: #f4f4f4; */
  color: #3b3b3c;
  font-family: 'Source Sans Pro', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #f4f4f4;
  min-width: 100%;
  min-height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #3b3b3c;
  font-size: 12px;
}

.col {
  min-width: 400px;
  text-align: left;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

button {
  min-height: 20px;
  align-self: end;
}





.BodyComponent .center-col {
  width: 700px; }

.BodyComponent .right-col {
  padding-left: 40px;
  height: 500px;
  overflow-y: auto;
  max-width: 300px; }

.BodyComponent .filtersRow {
  background-color: #f5f5f5;
  min-width: 100%;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
  margin-bottom: 10px;
  color: #3b3b3c;
  font-size: 12px; }

.BodyComponent .yearsSlider .rc-slider-track {
  background-color: #f48320; }

.BodyComponent .yearsSlider .rc-slider-handle {
  border: solid 2px #f48320; }

.BodyComponent .yearsSlider .rc-slider-handle:hover {
  border-color: #f48320; }

.BodyComponent .yearsSlider .rc-slider-handle:focus {
  box-shadow: 0 0 0 5px #f48320ad; }

.BodyComponent .yearsSlider .rc-slider-dot-active {
  border-color: #f48320; }



.MapComponent .MapComponentMap .mapboxgl-canvas {
  top: 0; }

